<template>
    <section id="news-content">
        <div class="container">
            <h2 class="newsInfo-title">
                {{newsInfo.title}}
            </h2>
            <p class="newsInfo-description">
                {{newsInfo.description}}
            </p>

            <p class="newsInfo-text" v-html="newsInfo.f_text">
             
            </p>
            <router-link to="/">
              <Button text_plc="Вернуться назад" color="blue" class="about-btn" :size="buttonSize" />
            </router-link>
            
        </div>
       
    </section>
</template>

<script>
    import Button from '@/components/Button.vue';
    let host = 'https://krestiki.h1n.ru/api';
    async function getData(url = '') {
        const response = await fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        });
        return await response.json(); 
    }
    export default {
        components: {
            Button
        },
        props: {
            id: {
                required: true
            },
        },

        data() {
            return {
                newsInfo: {
                    title: 'Загрузка новости',
                    description: 'Пожалуйста, подождите',
                    f_text: 'Новость скоро загрузится'
                },
                buttonSize: {
                    width: '100%',
                    height: '35px',
                },
            }
        },

        methods: {
            async setID() {
                if(this.$props.id) {
                    let res = this.$props.id;
                    this.newsInfo.id = res;
                }
            },
            async getNews() {
                let res = await getData(host + '/getnews/' + this.$route.params.id);
                this.newsInfo = res;
            },
            goToHome(id) {
                alert('test');
                this.$router.push({
                    name: 'home',
                    params: {id},
                });
            }
        },

        mounted() {
            this.getNews();
        },

        computed: {
         
        }
    }
</script>

