  </section>
</template>

<script>
export default {
  data() {
    return {};
  },

  methods: {
    frameHeight(id) {
      var height = jQuery(id).contents().find("html").height();
      jQuery(id).height(height);
    },
  },

  mounted() {
    window.scrollTo(0, 150)

  },

  computed: {},
};
</script>

