<template>
    <section id="quiz">
        <div class="container">
             <div class="firstQuestion" v-show="sectionSelect === null">
                <h2 class="question-title">
                    {{firstQuestion.question}}
                </h2>
                <div class="answers">
                    <form action="#" @submit="setSection">
                        <label class="answerRow" v-for="(answer, index) in firstQuestion.answers" :key="index">
                            <input type="radio" name="answer" :value="answer.section" required>
                            {{answer.text}}
                        </label>
                        <button class="button button-next" type="submit" >Ответить</button>
                    </form>
                </div>
            </div>

            <div class="profTest">
                <div class="sections" v-for="(section, index) in sectors" :key="index">
                    <div class="section_item" v-show="section.name === sectionSelect">
                      <div class="questions" v-for="(question, indexQ) in section.questions" :key="indexQ">
                      <div v-if="!finishTest">
                            <div class="question-item"  v-show="indexQ === questionIndex">
                                <p>Вопрос {{questionIndex+1}}</p>
                                {{question.question}}
                                    <div class="flex-wrap wrap-buttons" style="display: flex; justify-content: space-between">
                                        <button id="no-button" class="button button-no" type="submit" @click="setAnswer($event, question.spec_code, section)">Нет</button>
                                        <button id="yes-button" class="button button-yes" type="submit" @click="setAnswer($event, question.spec_code, section)">Да</button>
                                    </div>
                            </div>
                         </div>
                      </div>
                      <div v-if="finishTest">
                        <h2>Результат тестирования</h2>
                        <div class="h3-desc">Вот какие специальности Вам подходят:</div>
                            <div class="spec-list"> 

                        
                                    <div class="spec-item" v-for="(item) in resultSpecialty" :key="item.id" @click="goToSpec(item.id)">
                                        <div class="code">
                                            {{item.code}}
                                        </div>
                                        <div class="title" style="font-weight: bold;">
                                        
                                            {{item.name}}
                                        </div>
                                        <button type="button" class="moreAboutSpec">Узнать подробнее</button>
                                        <div class="icon" :style="{'background-image': `url('${item.icon}')`}"></div>
                                    </div>
                              
                
                            </div> 
                      </div>
                      
                        
                    </div>
                </div>
            </div>
        </div>
        
    </section>
</template>

<script>
let host = 'https://krestiki.h1n.ru/api';
    async function getData(url = '') {
        const response = await fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        });
        return await response.json(); 
    }
    async function setData(url = '', data) {
        const response = await fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data),
        });
        return await response.json(); 
    }

    //перемешивает массив по алгоритму Фишера-Йетса
    function shuffle(arr){
        var j, temp;
        for(var i = arr.length - 1; i > 0; i--){
            j = Math.floor(Math.random()*(i + 1));
            temp = arr[j];
            arr[j] = arr[i];
            arr[i] = temp;
        }
        return arr;
    }
    export default {
        data() {
            return {
                specialtyList: null,
                firstQuestion: {
                    question: 'Выберите утверждение, наиболее близкое Вам',
                    answers: [
                        {
                            section: 'it',
                            text: "Мне нравится разрабатывать что-то новое, думать. Я люблю трудности и мне интересно решать сложные задачи"
                        },
                        {
                            section: 'design',
                            text: "Я творческий человек, много рисую, создаю что-то креативное. У меня развитый художественный вкус"
                        },
                        {
                            section: 'engineer',
                            text: "Мне нравятся сложные механизмы и устройства. Я люблю разбирать их, делать что-то свое, конструировать и строить. Мне нравится облегчать жизнь при помощи функциональных устройств"
                        },
                        {
                            section: 'transport',
                            text: "Мне нравится все, связанное с транспортом: я люблю автомобили, поезда или беспилотники, хочу разобраться в том, как они устроены и как ими управлять. Кроме того, мне нравится возможность планировать и управлять доставкой грузов на различном транспорте"
                        },
                        {
                            section: 'social',
                            text: "Мне нравится работа с людьми разного возраста: как с детьми, так и со взрослыми. Я люблю помогать людям или обучать их. Могу сказать о себе, что я многозадачный и стрессоустойчивый человек"
                        }
                    ],
                },
                sectors: {
                    itSector: {
                        name: 'it',
                        questions: [
                           
                        ],
                    },
                     designSector: {
                        name: 'design',
                        questions: [
                            
                        ],
                    },
                     socialSector: {
                        name: 'social',
                        questions: [
                            
                        ],
                    },
                     transportSector: {
                        name: 'transport',
                        questions: [
                            
                        ],
                    },
                     engineerSector: {
                        name: 'engineer',
                        questions: [
                           
                        ],
                    },
                },
                finishTest: false,
                sectionSelect: null,
                questionIndex: 0,
                testResult: [],
                answers: {},

                resultSpecialty: [],
                resultCodes: []
            }
        },

        methods: {
            setSection(event) {
                event.preventDefault();
                let form = event.target;

                let active = form.querySelector('input:checked');
                this.sectionSelect = active.value;
            },
            setAnswer(event, spec_code, section) {
                event.preventDefault();
                let button = event.target;
                if(button.id == "no-button") {
                    if(!this.answers[spec_code]) {
                        this.answers[spec_code] = 0;
                    }
                    this.answers[spec_code] -= 1;
                
                }
                if(button.id == "yes-button") {
                    if(!this.answers[spec_code]) {
                        this.answers[spec_code] = 0;
                    }
                    this.answers[spec_code] += 1;
       
                }
                
                this.questionIndex++;
                if(this.questionIndex >= section.questions.length) {
                    this.finishTest = true;
                   

                }
                this.getTestResult();
            },
            async getTestResult(answers = this.answers) {
                let self = this;
                let spec = [];
                let array = Object.values(answers);
                let max = Math.max(...array);
                for(let res in answers) {
                    if(answers[res] === max) {
                        spec.push(res);
                    }
                };

                this.testResult = spec;
                this.resultSpecialty = [];
          
                this.testResult.forEach(function(code) {
                    //console.log(code);
                    self.specialtyList.map(function(specialtyRes) {
                        if(specialtyRes.code == code) {
                           self.resultSpecialty.push(specialtyRes);

                           
                        }
                       
                    });
                });
                
                if(this.finishTest == true) {
                    let nameAbit =  localStorage.getItem('abit-name');
                    let classAbit = localStorage.getItem('abit-class');
                    this.testResult.forEach(function(code) {
                        //console.log(code);
                        self.specialtyList.map(function(specialtyRes) {
                            if(specialtyRes.code == code) {
                                console.log(specialtyRes.code);
                                self.resultCodes.push(specialtyRes.code);

                            
                            }
                        
                        });
                    });
                    const response = await setData(host + '/setanswer', {
                        name: nameAbit,
                        class: parseInt(classAbit),
                        result: self.resultCodes.toString(),
                    });
                }

            },

            async getQuestions() {
                const response = await getData(host + '/questions');
                let self = this;
                response.map(function(a) {
                    if(a.section == 'it') {
                        self.sectors.itSector.questions.push(a);
                    }
                    if(a.section == 'design') {
                        self.sectors.designSector.questions.push(a);
                    }
                    if(a.section == 'social') {
                        self.sectors.socialSector.questions.push(a);
                    }
                    if(a.section == 'transport') {
                        self.sectors.transportSector.questions.push(a);
                    }
                    if(a.section == 'engineer') {
                        self.sectors.engineerSector.questions.push(a);
                    }
                });

                if(this.sectors.itSector.questions.length != 0 && this.sectors.designSector.length != 0  && this.sectors.socialSector.length != 0  && this.sectors.transportSector.length != 0  && this.sectors.engineerSector.length != 0 ) {
                    this.sectors.itSector.questions = shuffle(this.sectors.itSector.questions);
                    this.sectors.designSector.questions = shuffle(this.sectors.designSector.questions);
                    this.sectors.transportSector.questions = shuffle(this.sectors.transportSector.questions);
                    this.sectors.socialSector.questions = shuffle(this.sectors.socialSector.questions);
                    this.sectors.engineerSector.questions = shuffle(this.sectors.engineerSector.questions);
                } 
               
            },

             async getSpecialty() {
                const response = await getData(host + '/getspecialty');
                this.specialtyList = response;
               
            },
            
            goToSpec(id) {
                this.$router.push({
                    name: 'specialtyOne',
                    params: {id},
                });
            }
 
        },
        mounted() {
            this.getQuestions();
            this.getSpecialty();
 
        },

        
        computed: {
           
        }
    }
</script>

<style lang="scss" scoped>
    .answerRow {
        display: block;
        margin-bottom: 10px;
    }

   .button {

        width: 420px;
        max-width: 100%;
        min-height: 32px;
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1), 0px 0px 6px rgba(0, 0, 0, 0.06);
        border: none;
        padding: 0 20px;
        border-radius: 5px;
        box-sizing: border-box;
        font-family: inherit;
        background: linear-gradient(180deg, #0041EA 0%, #5383FF 210.71%);
        color: #fff;
    
    }
    .button:active {
        box-shadow: inset 0px 4px 16px rgba(0, 0, 0, 0.1), 0px 0px 6px rgba(0, 0, 0, 0.26);
    }

    .button-no {
        width: 49%;
        background: #e74c3c;
    }

    .button-yes {
        width: 49%;
        border-radius: 2px;
        background: #27ae60;
    }

    .wrap-buttons {
        margin-top: 10px;
    }

    
    .h3-desc {
        font-size: 14px;
        margin-top: -10px;
    }


    .spec-item {
        width: 332px;
        min-height: 80px;
   
        border-radius: 15px;
        background: linear-gradient(180deg, #5383FF 0%, #0041EA 100%);
        color: #080808;;
        padding: 30px 20px;
        box-sizing: border-box;
        position: relative;
        margin: 0px 7px;
        flex-shrink: 0;
        cursor: pointer;
         color: #fff;
         margin-top: 30px;
    }

    .moreAboutSpec {
        width: 85%;


    
        min-height: 28px;
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1), 0px 0px 6px rgba(0, 0, 0, 0.06);
        border: none;
        padding: 0 20px;
        border-radius: 25px;
        box-sizing: border-box;
        font-family: inherit;
        background: #fff;
        color: #000;
        font-weight: bold;
      margin-top: 20px;
    }

</style>