<template>
    <section id="quiz-page">
        <Quiz />
    </section>
</template>

<script>

    import Quiz from '@/components/Quiz/Quiz.vue';
    export default {
        components: {
            Quiz
        },
        methods: {
            setParams() {
                let classNum = localStorage.getItem('abit-class');
                let abitName = localStorage.getItem('abit-name');

                this.$route.params.class = classNum;
                this.$route.params.name = abitName;
            }
        },
        mounted() {
            this.setParams();
        }
    }
</script>

<style lang="scss" scoped>
    #quiz-page {
        margin-top: 15px;
    }
</style>