<template>
    <section id="spec-content">
        <div class="container">
            <h2 class="specInfo-title section-title">
                {{specInfo.name}}
            </h2>
            <p class="specInfo-description">
                {{specInfo.code}}
            </p>

            <p class="specsInfo-text">
                {{specInfo.f_text}}
            </p>

            <div class="section-title">
                <h3 style="margin-top: 30px; margin-bottom: 5px;">Профессиограмма</h3>
                <div class="h3-desc">Нажмите, чтобы открыть!</div>
            </div>
            <a :href="specInfo.professiogramm" class="professiogramm-block" @click.prevent="showImage(specInfo.professiogramm)">
             <img class="professiogramm-image" :src="specInfo.professiogramm" alt="Профессиограмма">
            </a>
            <a @click="$router.go(-1)">
              <Button text_plc="Вернуться назад" color="blue" class="about-btn" :size="buttonSize" />
            </a>
            
        </div>

        <div class="showImageModal" v-if="modal.isShowModal">
            <div class = "imageModal">
                <img :src="modal.imageModal">
            
            </div>
         
            <button type="button" id="button-close" class="close-btn blue"  style="position: fixed; bottom: -10px; left: 50%; transform: translateX(-50%)" @click="closeModal()" >Закрыть</button>
       </div>
    </section>
</template>

<script>
    import Button from '@/components/Button.vue';
    let host = 'https://krestiki.h1n.ru/api';
    async function getData(url = '') {
        const response = await fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        });
        return await response.json(); 
    }
    export default {
        components: {
            Button
        },
        props: {
            id: {
                required: true
            },
        },

        data() {
            return {
                specInfo: {
                    title: 'Загрузка новости',
                    description: 'Пожалуйста, подождите',
                    f_text: 'Новость скоро загрузится'
                },
                buttonSize: {
                    width: '100%',
                    height: '35px',
                },
                modal: {
                    isShowModal: false,
                    imageModal: String

                }
            }
        },

        methods: {
            async setID() {
                if(this.$props.id) {
                    let res = this.$props.id;
                    this.specInfo.id = res;
                }
            },
            async getSpec() {
                let res = await getData(host + '/getspecialty/' + this.$route.params.id);
                this.specInfo = res;
            },
            goToHome(id) {
                alert('test');
                this.$router.push({
                    name: 'home',
                    params: {id},
                });
            },

            showImage(image) {
                this.modal.imageModal = image;
                this.modal.isShowModal = true;
            },

            closeModal() {
         
                this.modal.isShowModal = false;
            }
        },

        mounted() {
            this.getSpec();
        },

        computed: {
         
        }
    }
</script>

<style lang="scss" scoped>
// Кнопка
    #button-close {

        width: 420px;
        min-height: 35px;
        box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.1), 0px 0px 6px rgba(0, 0, 0, 0.06);
        border: none;
        padding: 0 20px;
        border-radius: 98px;
        box-sizing: border-box;
        font-family: inherit;
    
    }
    #button-close:active {
        box-shadow: inset 0px 4px 16px rgba(0, 0, 0, 0.1), 0px 0px 6px rgba(0, 0, 0, 0.26);
    }



    #button-close.blue {
        background: linear-gradient(180deg, #0041EA 0%, #5383FF 210.71%);
        color: #fff;
    }


//--> Кнопка
    .professiogramm-image {
        width: 100%;
        height: 200px;
        object-fit: cover;
        object-position: 0px -72px;
        cursor: pointer;
    }

    .professiogramm-block {
        height: 200px;
        position: relative;
        display: block;
        text-decoration: none;
    }
    // .professiogramm-image:hover {
    //     position: absolute;
    //     width: 105%;
    //     height: 205px;

    //     transition: 5s;
    // }
    .h3-desc {
        font-size: 12px;
    }

    .section-title {
        margin-bottom: 10px;
    }

    .showImageModal {
        width: 95%;
        height: 85%;
        position: fixed;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 99000;
        background: #fff;
    }

    .showImageModal img {
        max-width: 100%;
    }

    .imageModal {
        overflow-y: scroll;
        height: 100%;
    }
</style>