<template>
    <section id="news-page">
        <NewsPage :id="newsInfo.id" />
    </section>
</template>

<script>
import NewsPage from '@/components/News/NewsPage.vue';
    export default {
        components: {
            NewsPage
        },
        data() {
            return {
                newsInfo: {
                    id: null,
                },
            }
            
        },
        methods: {
            getId() {
                this.newsInfo.id = this.$route.params.id;
            }
        },
        mounted() {
            this.getId();
        }
    }
</script>

