<template>
  <section id="rating-content">
    <div class="container">
      <h2 class="section-title">
        Рейтинг абитуриентов
      </h2>
      <p>
        Листайте таблицу вправо и влево
      </p>
      <div class="table-scroll-wrap table-responsive">
        <table class="table table-bordered tableRating table-sm ">
          <tbody class="tableRatingSpec">
            <tr class="nameRow">
              <td class="nameSpec" colspan="5"></td>
              <td class="baze" colspan="2"></td>
              <td class="form"></td>
            </tr>
            <tr class="headRow">
              <th class="pp">П/П</th>
              <th class="position">Позиция</th>
              <th class="document">Документ</th>
              <th class="prior">Приоритет</th>
              <th class="fio">ФИО</th>
              <th class="score">Балл</th>
              <th class="vbv">Б/ВБ</th>
              <th class="dateCreate">Дата заявки</th>
            </tr>
            <tr class="abitRow">
              <td class="pp">П/П</td>
              <td class="position">Позиция</td>
              <td class="document">Документ</td>
              <td class="prior">Приоритет</td>
              <td class="fio">ФИО</td>
              <td class="score">Балл</td>
              <td class="vbv">Б/ВБ</td>
              <td class="dateCreate">Дата заявки</td>
            </tr>
          </tbody>
          <thead id="content"></thead>
        </table>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {};
  },

  methods: {},

  mounted() {
    var data = "";
    jQuery(document).ready(function () {
      jQuery.ajax({
        type: "GET",
        url: "http://asu.samgk.ru/abiturient.php",
        crossDomain: true,
        data: {
          rating: "yes",
          page: "yes",
        },
        dataType: "json",
        success: function (otvet) {
          jQuery(".date").text(otvet.date);
          data = otvet.rating;

          for (var i = 0; i < data.length; i++) {
            var table = jQuery(".tableRatingSpec:eq(0)").clone();
            jQuery(table)
              .find(".tableRatingSpec")
              .removeClass("tableRatingSpec");
            jQuery(table).find(".nameSpec").text(data[i].nameSpec);
            jQuery(table)
              .find(".baze")
              .text("База приёма " + data[i].baze + " классов");
            jQuery(table).find(".form").text(data[i].form);
            jQuery(table).find(".abitRow").remove();
            if (data[i].abits !== null) {
              for (var abit = 0; abit < data[i].abits.length; abit++) {
                var pp = abit + 1;
                var row = jQuery(".abitRow:eq(0)").clone();
                jQuery(row).find(".pp").text(pp);
                jQuery(row).find(".position").text(data[i].abits[abit].pos);
                jQuery(row)
                  .find(".document")
                  .text(data[i].abits[abit].document);
                jQuery(row).find(".prior").text(data[i].abits[abit].prior);
                jQuery(row).find(".fio").text(data[i].abits[abit].family);
                jQuery(row).find(".score").text(data[i].abits[abit].score);
                jQuery(row).find(".vbv").text(data[i].abits[abit].bvb);
                jQuery(row)
                  .find(".dateCreate")
                  .text(data[i].abits[abit].dateCreate);
                jQuery(table).append(row);
              }

              jQuery(".tableRating").append(table);
            }
          }

          jQuery(".tableRatingSpec:eq(0)").remove();
        },
      });
    });
  },

  computed: {},
};
</script>

<style scoped>
table {
  table-layout: auto;
}
.nameRow {
  border-top: 2px solid #000;
}

.table-scroll-wrap {
  overflow-x: scroll;
  max-width: 100%;
}
</style>
