<template>
    <section id="specialty-page">
        <SpecialtyPage :id="specialtyInfo.id" />
    </section>
</template>

<script>
import SpecialtyPage from '@/components/Specialty/SpecialtyPage.vue';
    export default {
        components: {
            SpecialtyPage
        },
        data() {
            return {
                specialtyInfo: {
                    id: null,
                },
            }
            
        },
        methods: {
            getId() {
                this.specialtyInfo.id = this.$route.params.id;
            }
        },
        mounted() {
            this.getId();
        }
    }
</script>

