            </div>
            
            <router-link to="/">
              <Button text_plc="Вернуться на главную" color="blue" class="about-btn" :size="buttonSize" />
            </router-link>
            
        </div>
       
    </section>
</template>

<script>
    import Button from '@/components/Button.vue';
    let host = 'https://krestiki.h1n.ru/api';
    async function getData(url = '') {
        const response = await fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            },
        });
        return await response.json(); 
    }
    export default {
        components: {
            Button
        },
      
        data() {
            return {
                specInfo: [{
                    title: 'Загрузка новости',
                    description: 'Пожалуйста, подождите',
                    f_text: 'Новость скоро загрузится'
                }],
                buttonSize: {
                    width: '100%',
                    height: '35px',
                },
            }
        },

        methods: {
           
            async getSpec() {
                let res = await getData(host + '/getspecialty');
                this.specInfo = res;
            },
            goToHome(id) {
                alert('test');
                this.$router.push({
                    name: 'home',
                    params: {id},
                });
            },

            goToSpec(id) {
                this.$router.push({
                    name: 'specialtyOne',
                    params: {id},
                });
            }
        },

        mounted() {
            this.getSpec();
        },

        computed: {
         
        }
    }
</script>

